import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-words',
  templateUrl: './words.component.html',
  styleUrls: ['./words.component.css']
})
export class WordsComponent implements OnInit {

  newWordEn: '';
  newWordVn: '';
  filterStatus = 'getAll';
  isAdded= false;
  arrWord = [
    {id: 1, en: 'action', vn: 'hành động', memorized: true},
    {id: 2, en: 'actor', vn: 'diễn viên', memorized: false},
    {id: 3, en: 'activity', vn: 'hoạt động', memorized: true},
    {id: 4, en: 'active', vn: 'chủ động', memorized: true},
    {id: 5, en: 'bath', vn: 'tắm', memorized: false},
    {id: 6, en: 'bedroom', vn: 'phòng ngủ', memorized: true},

  ];
  addWord(){
    this.arrWord.push({
      id: this.arrWord[this.arrWord.length - 1].id + 1 , en: this.newWordEn, vn: this.newWordVn, memorized: false
    })
    this.newWordEn = '';
    this.newWordVn = '';
  }
  addForm(){
    this.isAdded = !this.isAdded;
    if(!this.isAdded){
      this.addWord();
    }
  }
  deleteWord(id: number){
   const index = this.arrWord.findIndex(word => word.id === id);
   alert("delete " + index)
   this.arrWord.splice(index, 1);
  }

  getShowStatus(memorized: boolean){
    const getAll = this.filterStatus === 'getAll';
    const getTrue = this.filterStatus === 'getTrue'&& memorized;
    const getFlase = this.filterStatus === 'getFlase'&&!memorized;
    return getAll || getFlase || getTrue;
  }

  

  
  constructor() { }

  ngOnInit() {
  }

}
