import { Component, OnInit } from '@angular/core';
import { IpService } from './ip.service'

@Component({
  selector: 'app-ip',
  template: `
  <form>
  <h3> {{ getMessage() }}</h3>
 <table border="1px" cellpadding="0px" cellspacing="0px" width="50%">
 <tr>
 <th>Id</th>
 <th>UserName</th>
 <th>CreatedAt</th>
 <th>UpdateAt</th>
 <th colspan="2">Acction</th>
 </tr>
 <ng-container *ngFor="let data of datas" > 
 <tr>
 <th>{{ data.id }}</th>
 <th>{{ data.username }}</th>
 <th>{{ data.createdAt | date: 'dd/MM/yyy'}}</th>
 <th>{{ data.updateAt | date: 'dd/MM/yyy' }}</th>
 <th><button (click)=onChange(data)>Change Pass</button></th>
 <th><button (click)="deleteUser(data);" >Delete User</button></th>
 </tr>
 </ng-container>
 </table>
  </form>

  <div *ngIf="isShowForm" >
  
  <h2>Fix User {{ email }}</h2>
  <form (submit)="onSubmit(formchange);" #formchange="ngForm">
  <span
  type="hidden"
  [(ngModel)]="Id"   
  name="Id" 
  ></span>
  
  <input 
  type="hidden"
  placeholder="Email" 
  [(ngModel)]="email"   
  #txtEmail="ngModel" 
  name="Username" 
  required
  email
  
  >
  <p *ngIf="txtEmail.touched && txtEmail?.errors?.required">
  Email is required
  </p>
  <p *ngIf="txtEmail.touched && txtEmail?.errors?.email">
  Email is not valid
  </p>

  <p>Password:</p>
  <input 
  type="password" 
  placeholder="Password" 
  [(ngModel)]="password" 
  #txtPassword="ngModel"
  name="password"
  minlength="6"
  pattern="[0-9]*"
  required
  >
  <br>
  <p *ngIf="txtPassword.touched && txtPassword?.errors?.required">
  Password is required
  </p>
  <br>
  <button [disabled]="formchange.invalid" >Submit</button>
  </form>
  <p>{{ formchange.value | json }}</p>
  <p>{{ txtPassword.errors | json }}</p>
  <br><br>
  </div>

  `,
})

export class IpComponent implements OnInit {
  datas = [];
  Id = '';
  password = '';
  email = '';
  isShowForm = false;
  isLoading = false;
  isDone = false;
  constructor(private ipService: IpService) { }

  ngOnInit(): void {

  }

  getData() {
    this.isLoading = true;
    this.delay(500).then(any => {
      this.ipService.getData()
        .then(data => {
          this.datas = data;
          this.isLoading = false;
        })
        .catch(err => console.log(err));
    });

  }

  deleteUser(user: any) {
    console.log(user);
    this.ipService.sendDelete(user)
      .then(data => this.getData())
      .catch(err => console.log(err));
  }

  getMessage() {
    if (this.isLoading) {
      return 'Loading...';
    }
    if (this.isDone) {
      return 'Update User ' + this.email + 'Success!!!'
    }

  }

  onSubmit(formchange: any) {
    console.log(formchange);
    this.ipService.changeUser(formchange.value)
      .then(result => this.isDone = true)
      .catch(err => console.log(err));
    this.isShowForm = false;
    this.getData();
  }
  onChange(data: any) {
    console.log(data);
    this.isShowForm = true;
    this.email = data.username;
    this.password = data.password;
    this.Id = data.id;
  }

  async delay(ms: number) {
    await new Promise(resolve => setTimeout(() => resolve(), ms)).then(() => console.log("ok"));
  }
}
