import { Component, ViewChild } from '@angular/core'
import { SignInService } from './sign-in.service'
import { IpComponent } from './ip.component'

@Component({
    selector: 'app-sign-in',
    template: `
    <h2>Create new user</h2>
    <form (submit)="onSubmit(formSignIn);" #formSignIn="ngForm">
    <input 
    placeholder="Email" 
    ngModel
    #txtEmail="ngModel" 
    name="Username" 
    required
    email
    >
    <p *ngIf="txtEmail.touched && txtEmail?.errors?.required">
    Email is required
    </p>
    <p *ngIf="txtEmail.touched && txtEmail?.errors?.email">
    Email is not valid
    </p>
    <br><br>
    <input 
    type="password" 
    placeholder="Password" 
    ngModel 
    #txtPassword="ngModel"
    name="password"
    minlength="6"
    pattern="[0-9]*"
    required
    >
    <br>
    <p *ngIf="txtPassword.touched && txtPassword?.errors?.required">
    Password is required
    </p>
    <br>
    <button [disabled]="formSignIn.invalid" >Submit</button>
    </form>
    <p>{{ formSignIn.value | json }}</p>
    <p>{{ txtPassword.errors | json }}</p>
    <br><br>


    

    <button (click)="onGetData();">GetUsername</button>
    <app-ip></app-ip>

    

    

    `,
    providers: [SignInService]
})

export class SignInComponent {
    @ViewChild(IpComponent)
    myIp: IpComponent;
    isPending = false;

    constructor(private signInService: SignInService) { }

    onSubmit(formSignIn) {
        this.signInService.sendPost(formSignIn.value)
            .then(result => console.log(result))
            .catch(err => console.log(err));
    }

    onGetData(){
       this.myIp.getData();
    }



    




}